<template>
  <a-modal
    :visible="visible"
    :title="hasEvent ? '' : ''"
    :okText="hasEvent ? '' : ''"
    cancelText=""
    @cancel="$emit('cancel')"
    @keyup.enter="onSubmit"
    @ok="onSubmit"
  >
    <a-form-model layout="vertical" ref="form" :model="form" :rules="rules">
      <a-form-model-item label="" prop="name">
        <a-input v-model="form.name" @change="onChange" @pressEnter="onSubmit">
          <a-dropdown
            slot="addonBefore"
            :trigger="['click']"
            v-model="setIconVisible"
          >
            <a-tooltip title="">
              <div class="setIcon">
                {{ form.icon }}
              </div>
            </a-tooltip>
            <VEmojiPicker
              slot="overlay"
              @select="selectEmoji"
              :i18n="i18n"
              :continuousList="true"
            />
          </a-dropdown>
        </a-input>
      </a-form-model-item>
      <a-form-model-item label="">
        <a-select label-in-value v-model="abbrType" @change="onChange">
          <a-select-option value="name">
            
          </a-select-option>
          <a-select-option value="pinyin">
            
          </a-select-option>
          <a-select-option value="initial">
            
          </a-select-option>
          <a-select-option value="user-defined">
            
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="" prop="abbr">
        <a-input
          v-model="form.abbr"
          :disabled="disabledInput"
          @pressEnter="onSubmit"
        />
      </a-form-model-item>
      <a-form-model-item label="" prop="description">
        <a-input v-model="form.description" @pressEnter="onSubmit" />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import VEmojiPicker from "v-emoji-picker";
//Event
// id: (...)
// name: (...)
// abbr: (...)
// description: (...)
// active: (...)
// star: (...)
export default {
  props: ["visible", "event"],
  components: {
    VEmojiPicker
  },
  data() {
    return {
      abbrType: { key: "initial" },
      disabledInput: true,
      setIconVisible: false,
      form: {
        name: "",
        abbr: "",
        description: "",
        icon: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              for (let id in this.events) {
                if (!this.hasEvent && this.events[id].name === value)
                  callback("");
              }
              callback();
            },
            trigger: "blur"
          }
        ]
      },
      i18n: {
        search: "",
        categories: {
          Activity: "",
          Flags: "",
          Foods: "",
          Frequently: "",
          Objects: "",
          Nature: "",
          Peoples: "",
          Symbols: "",
          Places: ""
        }
      }
    };
  },
  computed: {
    hasEvent() {
      return this.event && Object.keys(this.event).length !== 0;
    },
    ...mapGetters(["events"])
  },
  mounted() {
    if (this.hasEvent) {
      const { name, abbr, description, icon } = this.event;
      this.form = { name, abbr, description, icon };
      this.disabledInput = false;
      this.abbrType = { key: "user-defined" };
    }
  },
  methods: {
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.hasEvent) {
            this.updatedEvent({ id: this.event.id, ...this.form });
            this.$message.success("");
          } else {
            this.createEvent(this.form);
            this.$message.success("");
          }

          this.$emit("create");
          this.$refs.form.resetFields();
        } else {
          this.$message.error("");

          return false;
        }
      });
    },
    async onChange() {
      const type = this.abbrType.key;
      switch (type) {
        case "name":
          this.disabledInput = true;
          this.form.abbr = this.form.name;

          break;
        case "pinyin":
          this.form.abbr = await this.getPinyin(this.form.name);
          this.disabledInput = true;

          break;
        case "initial":
          this.disabledInput = true;
          this.form.abbr = await this.getInitial(this.form.name);

          break;
        case "user-defined":
          this.disabledInput = false;
          break;
      }
    },
    selectEmoji(emoji) {
      this.form.icon = emoji.data;
      this.setIconVisible = false;
      console.log(emoji);
    },
    ...mapActions(["createEvent", "updatedEvent", "getInitial", "getPinyin"])
  }
};
</script>
<style>
.setIcon {
  height: 3rem;
  width: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  cursor: pointer;
  font-size: 1.8rem;
}
</style>
